import * as Yup from 'yup';
export const signupValidationSchema = Yup.object().shape({
    name: Yup.string()
        .required('Tn khng c  trng')
        .max(50, 'Tn ti a 50 k t'),
    email: Yup.string()
        .required('Email khng c  trng')
        .email('Email khng ng nh dng'),
    password: Yup.string()
        .required('Mt khu khng c  trng')
        .min(3, 'Mt khu c t nht 3 k t')
        .max(40, 'Mt khu ti a 40 k t'),
    password_confirmation: Yup.string()
        .required('Yu cu nhp li mt khu')
        .oneOf([Yup.ref('password'), null], 'Nhp li mt khu khng khp'),
    phone: Yup.string()
        .required('Yu cu s in thoi')
        .matches(/^[0-9]+$/, "ST phi l s")
        .min(10, 'S in thoi kh  ng ng')
        .max(13, 'S in thoi khng ng- long')

});
export const loginValidationSchema = Yup.object().shape({
    email: Yup.string()
        .required('Email khng c  trng')
        .email('Email khng ng nh dng'),
    password: Yup.string()
        .required('Mt khu khng c  trng')
        .min(3, 'Mt khu c t nht 3 k t')
});
