import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import { withRouter } from "react-router";

import { useDispatch, useSelector } from "react-redux";

import { addItem } from "../../redux/shopping-cart/cartItemsSlide";
import { remove } from "../../redux/product-modal/productModalSlice";

import Button from "../button/Button";
import numberWithCommas from "../../utils/numberWithCommas";
import { success, warn } from "../noti/noti";
import ImageSlider from "../slider/ImageSlider";
import { isEmpty } from "../../utils/utils";
const noImages = require("../../assets/images/no-images.png").default;
const ProductView = (props) => {
  const dispatch = useDispatch();

  let product = props.product;
  const content =
    "S hin din ca nhng chic o thun basic c trn trong t  ca bn chnh l cha kha gip cho bn c thm nhiu outfit th v m li khng cn n qu nhiu mn . o thun n cotton c trn basic chnh l v kh tin dng cho cc ch em trong trang phc hng ngy!<br><br><br>Thit k n gin, form dng tin li ca o thun PPN4502. Ti sao ch vi 1 chic o thun n basic m bn c th phi vi 10 b  khc nhau? Cu tr li nm  chnh s n gin ca chng. Cng n gin, bn li cng d mix & match vi nhng mn  khc nhau.o thun n PPM4502 c thit k c trn n gin, nh nhng tn da. Tay co, form o cng khng h cu k, rt d mc vi nhiu thn hnh khc nhau. c bit hn, mu sc ca chic o phng n c trn ny cng rt nh nhn, trung tnh, trn mu. S ti gin t thit k, ng may n bng mu gip cc ch em khng cn n o qu nhiu khi la chn. Cht liu cotton 95% c x l nghim ngt, quy trnh v cng ngh hin i nn mang ti cho chic o s thoi mi, mm mi, thong mt ngay khi chm vo. Cng vi , o thun n cotton c trn Yody c kh nng thm shuts m hi rt tt nn ngi mc khng b cm gic b bch, dnh dnh trn da khi  m hi vo ma h. Bn cnh , sn phm cng cha 5% spandex - loi si gip co gin, n hi hiu qu thch hp mc ti nhiu mi trng, ngay c khi vn ng <br><br><br> S hin din ca nhng chic o thun basic c trn trong t  ca bn chnh l cha kha gip cho bn c thm nhiu outfit th v m li khng cn n qu nhiu mn . o thun n cotton c trn basic chnh l v kh tin dng cho cc ch em trong trang phc hng ngy!<br><br><br>Thit k n gin, form dng tin li ca o thun PPN4502. Ti sao ch vi 1 chic o thun n basic m bn c th phi vi 10 b  khc nhau? Cu tr li nm  chnh s n gin ca chng. Cng n gin, bn li cng d mix & match vi nhng mn  khc nhau.o thun n PPM4502 c thit k c trn n gin, nh nhng tn da. Tay co, form o cng khng h cu k, rt d mc vi nhiu thn hnh khc nhau. c bit hn, mu sc ca chic o phng n c trn ny cng rt nh nhn, trung tnh, trn mu. S ti gin t thit k, ng may n bng mu gip cc ch em khng cn n o qu nhiu khi la chn. Cht liu cotton 95% c x l nghim ngt, quy trnh v cng ngh hin i nn mang ti cho chic o s thoi mi, mm mi, thong mt ngay khi chm vo. Cng vi , o thun n cotton c trn Yody c kh nng thm shuts m hi rt tt nn ngi mc khng b cm gic b bch, dnh dnh trn da khi  m hi vo ma h. Bn cnh , sn phm cng cha 5% spandex - loi si gip co gin, n hi hiu qu thch hp mc ti nhiu mi trng, ngay c khi vn ng";
  if (product === undefined)
    product = {
      title: "",
      price: "",
      image01: null,
      image02: null,
      categorySlug: "",
      colors: [],
      slug: "",
      size: [],
      description: "",
    };

  const [previewImg, setPreviewImg] = useState(product.images[0]);

  const [descriptionExpand, setDescriptionExpand] = useState(false);

  const [color, setColor] = useState(undefined);

  const [size, setSize] = useState(undefined);

  const [quantity, setQuantity] = useState(1);
  const products = useSelector((state) => state.product.products);
  const [rage, setRage] = useState({
    min: 0,
    max: 0,
    avg: 0,
  });
  useEffect(() => {
    if (product) {
      let min = 200,
        max = 300;

      if (max != min) {
        setRage({
          min: min,
          max: max,
        });
      } else {
        setRage({
          ...rage,
          avg: min,
        });
      }
    }
  }, [product]);

  const updateQuantity = (type) => {
    if (type === "plus") {
      if (quantity == product.quantity) {
        warn(" t s sn phm ti a");
      }
      setQuantity(quantity + 1);
    } else {
      setQuantity(quantity - 1 < 1 ? 1 : quantity - 1);
    }
  };

  useEffect(() => {
    setPreviewImg(product.images[0] ? product.images[0] : noImages);
    setQuantity(1);
    setColor(undefined);
    setSize(undefined);
  }, [product]);

  const check = () => {
    if (color === undefined) {
      warn("Vui lng chn mu sc!");
      return false;
    }

    if (size === undefined) {
      warn("Vui lng chn kch c!");
      return false;
    }

    return true;
  };

  const addToCart = () => {
    if (check()) {
      if (product.quantity == 0) {
        warn("Mat hang nay da het");
      } else {
        let newItem = {
          slug: product.id,
          color: color,
          size: size,
          price: product.price,
          quantity: quantity,
          image: product.images[0],
          name: product.name,
        };
        if (dispatch(addItem(newItem))) {
          success("Them vao gio hang thanh cong!");
        } else {
          warn("The mat hang that bai");
        }
      }
    }
  };

  const goToCart = () => {
    if (check()) {
      if (product.quantity == 0) {
        warn("Mat hang nay da het");
      } else {
        let newItem = {
          slug: product.custom_id,
          color: color,
          size: size,
          price: product.price,
          quantity: quantity,
          image: product.images[0],
          name: product.name,
        };
        if (dispatch(addItem(newItem))) {
          dispatch(remove());
          props.history.push("/cart");
        } else {
          alert("Fail");
        }
      }
    }
  };

  return (
    <div className="product">
      <div className="product__image__info">
        <div className="product__images">
          <div className="product__images__main">
            <img src={previewImg} alt="" />
          </div>
        </div>
        <div className="product__images_review">
          {!isEmpty(product.images) &&
            product.images.map((image, index) => (
              <div
                className="product__images_review_item"
                onClick={() => setPreviewImg(image)}
              >
                <img src={image} alt="" />
              </div>
            ))}
        </div>
        <div className="product__info">
          <h1 className="product__info__title">{product.name}</h1>
          <br />
          <h4>M sn phm : {product.sku}</h4>
          {/* <p>Cn li : {product.quantity} sn phm</p> */}
          <br />
        
          <div className="product__info__item">
            <span className="product__info__item__price">{product.price} VND</span>
          </div>
          <div className="product__info__item">
            <div className="product__info__item__title">Mu sc</div>
            <div className="product__info__item__list">
              {product.colors &&
                product.colors.map((item, index) => (
                  <div
                    key={index}
                    className={`product__info__item__list__item product__info__item__list__item__square ${
                      color === item ? "active" : ""
                    }`}
                    onClick={() => setColor(item)}
                  >
                    {/* <div className={`circle bg-main`}></div> */}
                    <div className="product__info__item__list__item__square">
                      {item}
                    </div>
                  </div>
                ))}
            </div>
          </div>
          <div className="product__info__item">
            <div className="product__info__item__title">Kch c</div>
            <div className="product__info__item__list">
              {product.sizes &&
                product.sizes.map((item, index) => (
                  <div
                    key={index}
                    className={`product__info__item__list__item product__info__item__list__item__circle ${
                      size === item ? "active" : ""
                    }`}
                    onClick={() => setSize(item)}
                  >
                    <span className="product__info__item__list__item__size ">
                      {item}
                    </span>
                  </div>
                ))}
            </div>
          </div>
          <div className="product__info__item">
            <div className="product__info__item__title">S lng</div>
            <div className="product__info__item__quantity">
              <div
                className="product__info__item__quantity__btn"
                onClick={() => updateQuantity("minus")}
              >
                <i className="bx bx-minus"></i>
              </div>
              <div className="product__info__item__quantity__input">
                {quantity}
              </div>
              <div
                className="product__info__item__quantity__btn"
                onClick={() => updateQuantity("plus")}
              >
                <i className="bx bx-plus"></i>
              </div>
            </div>
          </div>
          <br />
          <div className="product__info__item">
            <Button onClick={() => addToCart()}>thm vo gi</Button>
            <Button onClick={() => goToCart()}>mua ngay</Button>
          </div>
        </div>
      </div>

      <div
        className={`product-description ${descriptionExpand ? "expand" : ""}`}
      >
        <div className="product-description__title">Chi tit sn phm</div>
        <div
          className="product-description__content"
          dangerouslySetInnerHTML={{ __html: content }}
        ></div>
        <div className="product-description__toggle">
          <Button
            size="sm"
            onClick={() => setDescriptionExpand(!descriptionExpand)}
          >
            {descriptionExpand ? "Thu gn" : "Xem thm"}
          </Button>
        </div>
      </div>

      <div
        className={`product-description mobile ${
          descriptionExpand ? "expand" : ""
        }`}
      >
        <div className="product-description__title">Chi tit sn phm</div>
        <div
          className="product-description__content"
          dangerouslySetInnerHTML={{ __html: content }}
        ></div>
        <div className="product-description__toggle">
          <Button
            size="sm"
            onClick={() => setDescriptionExpand(!descriptionExpand)}
          >
            {descriptionExpand ? "Thu gn" : "Xem thm"}
          </Button>
        </div>
      </div>
    </div>
  );
};

ProductView.propTypes = {
  product: PropTypes.object,
};

export default withRouter(ProductView);
